@import url('https://fonts.googleapis.com/css2?family=K2D:ital,wght@0,200;0,400;0,600;1,700&display=swap');

.backup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #f2f2f2;
  }
  
  .backup-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f2f2f2;
    padding: 32px;
    border-radius: 4px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    width: 100%;
  }
  
  .backup-logo {
    width: 200px;
    margin-bottom: 16px;
  }
  
  .backup-title {
    font-family: 'K2D', sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 32px;
    color: #26255a;
  }

  
  .backup-form {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .backup-input-container {
    font-family: 'K2D', sans-serif;
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    color: #26255a;
  }
  
  .backup-input-label {
    
    margin-bottom: 8px;
    font-weight: bold;
    color: #26255a;
  }
  
  .backup-input {
    color: #26255a;
    font-family: 'K2D', sans-serif;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .backup-dropzone {
    font-family: 'K2D', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 96px;
    border: 2px dashed #26255a;
    border-radius: 4px;
    margin-bottom: 16px;
    cursor: pointer;
  }
  
  .backup-dropzone-text {
    color: #666;
  }
  
  .backup-button {
    font-family: 'K2D', sans-serif;
    padding: 12px 16px;
    background-color: #26255a;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.2s ease;
  }
  
  .backup-button:hover {
    
    background-color: #26255a;
  }
  
  .backup-progress-container {
    
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16px;
  }
  
  .backup-progress-bar 
  {
    font-family: 'K2D', sans-serif;
    height: 8px;
    background-color: #26255a;
    border-radius: 4px;
  }
  
  .backup-progress-text {
    font-family: 'K2D', sans-serif;
    margin-left: 8px;
    font-size: 14px;
    font-weight: bold;
    color: #26255a;
  }
  
  /* adição das cores em azul e branco conforme solicitado */
  
  body {
    background-color: #f2f2f2;
  }
  
  h2 {
    color: #26255a;
  }
  
  label {
    color: #26255a;
  }
  
  input[type="file"] {
    border-color: #26255a;
  }
  
  button[type="submit"] {
    background-color: #26255a;
  }
  
  button[type="submit"]:hover {
    background-color: #26255a;
  }
  
  .backup-dropzone {
    border-color: #26255a;
  }
  
  .backup-dropzone-text {
    color: #666;
  }
  
  .backup-progress-bar {
    background-color: #26255a;
  }
  
  .backup-progress-text {
    color: #26255a;
  }

.exitButtonDiv
{
  display: flex;
  align-items: center;
