.backup-disabled {
  pointer-events: none;
  opacity: 0.5;
}

.backup-progress-container {
  width: 100%;
  height: 20px;
  background-color: #f3f3f3;
  border: 1px solid #bbb;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  margin-top: 10px;
}

.backup-progress-bar {
  height: 100%;
  background-color: #0077b6;
  position: absolute;
  left: 0;
  top: 0;
}

.backup-progress-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  font-weight: bold;
  color: #000;
}
